.wrapper {
    height: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    background: #fff;
}
.header,
.footer {
    flex-shrink: 0;
}
.main {
    flex-grow: 1;
    padding-top: 95px;
    box-sizing: border-box;
}
.container {
    max-width: 1241px;
    width: 100%;
    box-sizing: border-box;
    margin: 0 auto;

    &.--wide {
        max-width: 1534px
    }
}

._scroll-lock {
    overflow: hidden !important;
}

.sitemap {
    .container {}

    .sitemap__wrapper {
        // padding-top: 100px;
        // display: flex;
        // justify-content: start;
        // align-items: center;
        // height: 100vh;


        ol {
            height: 100vh;
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            gap: 5px;
            padding: 5px;
            box-sizing: border-box;
            li {

                &:hover {
                    a {
                        color: #fff;
                        background: $main;
                    }
                }
                &:not(:last-child) {
                    // margin-bottom: 10px;
                }

                a {
                    width: 100%;
                    height: 100%;
                    font-size: 20px;

                    display: flex;
                    justify-content: center;
                    align-items: center;

                    border: 1px solid #dadada;
                    border-radius: 5px;
                    transition: .3s;
                    background: #f2f8fc;
                }
                ._disabled {
                    pointer-events: none;
                    opacity: .5;
                    color: #000;
                    background: #dadada;
                }
            }
        }
    }
}

