.header {
    background: #FFFFFF;
    box-shadow: 0px 4px 84px rgba(0, 0, 0, 0.25);

    .container {

    }
    .header__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;

        padding: 0 20px;
        box-sizing: border-box;
    }
    .header__wrap {
        display: flex;
        justify-content: start;
        align-items: center;
    }
    .header__burger {
        display: none;
        margin-right: 26px;

        &._active {
            .header__burger-wrap {
                &::before,
                &::after {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%) rotate(45deg);
                }
                &::after {
                    transform: translate(-50%, -50%) rotate(-45deg);
                }
                .header__burger-item {
                    opacity: 0;
                }
            }
        }
    }
    .header__burger-wrap {
        padding: 15px 12px;
        box-sizing: border-box;
        background: #FFFFFF;
        box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.25);
        border-radius: 10px;    
        
        display: flex;
        justify-content: center;
        align-items: center;

        position: relative;
        flex-direction: column;


        &::before,
        &::after {
            content: '';
            width: 27px;
            height: 2px;
            background: $main;
            display: block;

            transition: .3s;
        }
    }
    .header__burger-item {
        width: 27px;
        height: 2px;
        background: $main;
        margin: 5px 0;
        // transition: .3s;
    }
    .header__logo {
        display: block;
        max-width: 254px;
        width: 100%;
        margin-right: 40px;
        img {
            width: 100%;
        }
    }

    .header__mobile-list {
        display: flex;
        justify-content: start;
        align-items: center;

        &::before {
            content: '';
            display: block;
            position: absolute;
            top: 0px;
            left: 0;
            width: 100%;
            box-shadow: 0px 0px 9px rgba(0, 0, 0, 0.25);
            height: 1px;
            background: rgba(0, 0, 0, 0.1);
        }
    }
    .header__mobile-list__overlay {
        position: fixed;
        top: 80px;
        left: 0;
        width: 100vw;
        height: calc(100vh - 80px);
        background: rgba(38, 36, 93, .4);
        box-shadow: 0px 4px 84px rgba(0, 0, 0, 0.25); 
        z-index: 1;
        
        display: none;
    }
    .header__nav {
        margin-right: 49px;
    }
    .header__nav-list {
        display: flex;
        justify-content: start;
        align-items: center;
    }
    .header__nav-item {
        padding: 41px 0 44px;
        box-sizing: border-box;
        position: relative;

        &:not(:last-child) {
            margin-right: 30px;
        }
        & > a {
            font: inherit;
            font-weight: 500;
            text-transform: uppercase;
            text-decoration: none;
            color: #AAAAAA;
            transition: .3s;
        }
        &::after {
            content: '';
            width: 100%;
            height: 8px;
            background: $main;
            display: block;

            position: absolute;
            bottom: 0;
            left: 0;
            opacity: 0;
            transition: .3s;
        }

        &._active,
        &:hover {
            & > a {
                color: $main;
            }
            &::after {
                opacity: 1;
            }
        }
    }
    .header__search-wrap {

    }
    .header__search {
        position: relative;
    }
    .header__search-field {
        background: #FFFFFF;
        border: 1px solid #D0D0D0;
        border-radius: 8px;   
        padding: 14px 18px;
        box-sizing: border-box;
        outline: none !important;     

        font-style: normal;
        font-weight: 300;
        font-size: 18px;
        line-height: 21px;
        
        color: $main;        
    }
    .header__search-submit {
        border: none;
        background: transparent;
        outline: none;
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;

        width: 24px;
        height: 24px;   
        padding: 0;     

        position: absolute;
        top: 50%;
        right: 14px;
        transform: translateY(-50%);
        img {
            width: 100%;
            height: 100%;
            box-sizing: border-box;
        }
    }
    .header__btn {
        display: none;
    }
    .header__phone {
        color: $main;
        text-decoration: none;
        font-weight: 400;
        margin-left: auto;
    }
}

