@media screen and (max-width: 1565px) {
    .header .header__nav-item > a, .header .header__nav-item > a {
        color: #26245D;
    }
    .header .header__nav-item._active::after, .header .header__nav-item:hover::after {
        display: none;
    }
    .header .header__mobile-list {
        // display: none;

        position: fixed;
        top: 80px;
        left: 0;
        height: calc(100vh - 80px);

        background: #fff;
        box-shadow: 0px 5px 14px rgba(0, 0, 0, 0.25);
        flex-direction: column-reverse;

        max-width: 389px;
        width: 100%;
        padding-top: 27px;
        box-sizing: border-box;

        z-index: 1;
        transform: translateX(-1000px);
        transition: .3s;

        &._active {
            transform: translateX(0);
        }
    }
    .header__search-wrap {
        width: 100%;
        padding: 0 25px;
        box-sizing: border-box;
    }
    .header .header__search-field {
        width: 100%;
        border: 1px solid #D0D0D0;
    }
    .header .header__nav {
        margin-right: 0;
        width: 100%;
        padding: 0 25px;
        box-sizing: border-box;
    }
    .header .header__nav-list {
        flex-direction: column;
    }
    .header .header__nav-item {
        padding: 30px 0;
        width: 100%;
        border-bottom: 1px solid #D0D0D0;

        &:not(:last-child) {
            margin-right: 0;
        }
    }
    .header {
        padding: 14px 26px;
        box-sizing: border-box;
    }

    .header .header__burger {
        display: block;
    }
}

@media screen and (max-width: 1241px) {
    .container {
        padding: 0 25px;
    }
}

@media screen and (max-width: 1059px) {
    .detail .detail-2col {
        grid-template-columns: 1fr;
    }
    .main {
        padding-top: 54px;
    }
    .about .about__text {
        order: 2;
    }
    .about .about__img:last-child {
        margin-top: 27px;
        order: 1;
    }
    .about .about__wrapper {
        grid-template-columns: 1fr;
        gap: 35px;
    }
    .footer .footer__nav-mob {
        display: grid;
    }
    .footer {
        position: relative;
    }
    .footer__nav-item:nth-child(8),
    .footer__nav-item:nth-child(9) {
        display: none;
    }
    .footer .footer__copy {
        padding: 30px 0;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
    }
    .footer .footer__wrap {
        &:nth-child(1) {
            padding: 0 20px;
        }
        &:nth-child(2) {
            background: #323077; 
            padding: 30px 20px;           
        }
    }
    .footer .footer__nav-list {
        padding: 52px 20px 50px;
        grid-template-columns: 1fr 1fr;
        column-gap: 20px;
        row-gap: 32px;
        grid-template-rows: repeat(4, auto);
    }
    .footer .container {
        padding: 0;
    }

    .footer .footer__btn {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 1;
    }
    .header .header__burger {
        flex-shrink: 0;
    }
    .header .header__wrapper {
        padding: 0;
    }
    .container.--wide {
        padding: 0;
    }
    .footer {
        padding-top: 39px;
    }
    .footer .footer__wrapper {
        grid-template-columns: 1fr;
    }
    .footer__search-wrap {
        display: none;
    }
    .footer .footer__wrap {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
        align-items: center;

        &:first-child {
            padding-bottom: 32px;
        }
        &:nth-child(2) {
            // background: #323077;            
        }
    }
    .footer .footer__logo {
        max-width: 272px;
        margin-bottom: 0;
    }
    .footer .footer__text {
        margin-bottom: 0;
    }
}

