            margin-top: 10px;
        }
    }
    .footer .footer__wrap:first-child {
        padding-bottom: 19px;
        background: #323077;
        padding-top: 39px;
    }
    .footer {
        padding-top: 0;
    }
    .footer .footer__wrap {
        grid-template-columns: 1fr;
    }
    .header .header__logo {
        margin-right: 0;
    }
    .header .header__phone {
        display: none;
    }
    .header .header__burger {
        margin-right: 45px;
    }
    .header .header__logo {
        max-width: 171px;
    }
