@mixin page-h2() {
    font-style: normal;
    font-weight: 700;
    font-size: 35px;
    line-height: 41px;
    
    /* identical to box height */
    text-transform: uppercase; 
    color: $main;   

    padding-bottom: 30px;
    border-bottom: 1px solid #DADADA;

    @media screen and (max-width: 1059px) {
        font-size: 28px;
        line-height: 33px;       
    }
}
@mixin page-h3() {
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 38px;
    
    /* identical to box height */
    text-transform: uppercase; 
    color: $main;
    
    @media screen and (max-width: 1059px) {
        font-size: 18px;
        line-height: 21px;     
    }
}
@mixin page-text18() {
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    line-height: 26px;
    
    /* or 144% */
    
    color: $text;

    .b {
        font-weight: 700;
    }

    @media screen and (max-width: 1059px) {
        font-size: 14px;
        line-height: 16px;   
    }
}
@mixin page-text24() {
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    // text-transform: uppercase;

    @media screen and (max-width: 1059px) {
        font-weight: 300;
        font-size: 18px;
        line-height: 21px;        
    }
}

@mixin page-btn() {
    padding: 15px 40px;
    box-sizing: border-box;
    background: $main;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 5px;   
    
    text-align: center;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    
    /* identical to box height */
    text-transform: uppercase;
    
    color: #FFFFFF;   
    text-decoration: none;
    outline: none;
    border: none;
    
    display: inline-block;

    cursor: pointer;
}